
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: white;
  font-size: calc(10px + 2vmin);
}
.input-box {
  align-self: flex-end; 
  padding-right: 20px;
}

h1 {
  margin-bottom: 30px;
}

.input-section {
  display: flex;
  flex-direction: column;
  width: 90%;
  max-width: 600px;
  margin-bottom: 40px;
}

.text-input {
  font-size: 16px;
  padding: 10px;
  border-radius: 6px;
  border: none;
  resize: vertical;
  min-height: 80px;
  margin-bottom: 15px;
  font-family: inherit;
}

.send-button {
  background-color: #61dafb;
  border: none;
  border-radius: 6px;
  padding: 12px;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.send-button:hover {
  background-color: #21a1f1;
}

.result-section {
  width: 90%;
  max-width: 600px;
  text-align: left;
  color: black;
}

.translation-box {
  position: relative;
  background-color: #f4f4f4;
  padding: 20px 15px 15px;
  border-radius: 10px;
  margin-bottom: 20px;
  border: 2px solid #ccc;
}


.translation-label {
  position: absolute;
  top: -12px;
  left: 15px;
  background-color: #f4f4f4;
  padding: 0 8px;
  font-weight: bold;
  color: #333;
  font-size: 14px;
  pointer-events: none;
  border-radius: 4px;
  user-select: none;
}


.arabic-script {
  font-family: 'Scheherazade', serif;
  font-size: 20px;
  direction: rtl;
}

.transliteration {
  font-style: italic;
  font-size: 16px;
}

.line-pairs {
  display: flex;
  flex-direction: column;
  gap: 12px; /* space between each line pair */
}

.line-pair {
  display: flex;
  flex-direction: column;
}

.transliteration {
  font-style: normal;
  color: #333;
  margin: 0;
  line-height: 1.4;
}

.arabic-script {
  font-family: 'Scheherazade', serif;
  font-size: 1.2rem;
  color: #555;
  direction: rtl;
  margin: 0 0 8px 20px; 
  line-height: 1.4;
}

.spinner-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100px;
  margin-top: 20px;
}

.loader {
  border: 6px solid #f3f3f3;
  border-top: 6px solid #61dafb;
  border-radius: 50%;
  width: 48px;
  height: 48px;
  animation: spin 1s linear infinite;
}


.loader-button {
  border: 4px solid #f3f3f3;
  border-top: 4px solid #fff;
  border-radius: 50%;
  width: 18px;
  height: 18px;
  animation: spin 0.8s linear infinite;
  display: inline-block;
  vertical-align: middle;
}

.send-button:disabled {
  background-color: #a1cfe9;
  cursor: not-allowed;
}


@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}
